header {
  background-color: #fff;
  background-position: center;
  background-size: cover;
  height: auto;
  overflow: hidden;
  padding-bottom: 90px;

  .header-menu {
    nav {
      background-color: #fff;
      border-bottom: 1px solid #eee;
      box-shadow: 0px 0px 9px 0px rgba(0, 0, 0, 0.1);
      padding: 3px;
      margin: 0px;

      .container {
        .navbar-brand {
          background-color: transparent;
          width: 42px;
          height: 42px;
        }

        .navbar-toggler {
          position: relative;
          padding: 5px 10px;
          border: 1px solid #eee;
          font-size: 20px;

          .navbar-toggler-icon {}
        }

        .collapse {}

        .navbar-collapse {
          .navbar-nav {
            text-align: center;
            position: relative;

            .nav-item {
              transition: 0.3s ease-in-out;

              .nav-link {
                color: #006fe0;
                font-size: 16px;
                margin-left: 16px;
                padding: 7px;
                line-height: 40px;
                border-bottom: 2px solid transparent;

                &:hover,
                &:focus {
                  color: steelblue;
                }
              }
            }
          }
        }
      }
    }

    .navbar {
      background-color: white;
    }

    .navbar-expand-md > .container {
      padding-right: 15px;
      padding-left: 15px;
    }

    .navbar-light {}
  }

  .header-info {
    margin: 0;
    padding: 0;
    position: relative;
    padding-top: 90px;
    padding-bottom: 90px;

    h1 {
      font-size: 80px;
      color: $flat-blue;
    }

    .mission {
      text-align: center;
      text-transform: none;
      position: relative;
      font-size: 24px;
      color: #212121;

      span {
        color: $flat-blue;
        font-size: 25px;
      }
    }

    .arrow {
      background-color: transparent;
      width: 40px;
      height: 40px;
      margin: 0 auto;
      text-align: center;
      position: relative;
      margin-top: 75px;

      .zzz {}

      a>svg {
        background-color: transparent;
        margin: 0 auto;
        overflow: hidden;
      }
    }
  }
}