.ssize {
    background-color: orange;
    height: 42px;
}

.ihome {
    padding: 14px;
    font-size: 16px;
}

/* Test Collapse */

.sidebar {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #111;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 60px;
}

.sidebar a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
    transition: 0.3s;
}

.sidebar a:hover {
    color: #f1f1f1;
}

.sidebar .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
}

.openbtn {
    font-size: 20px;
    cursor: pointer;
    background-color: #111;
    color: white;
    padding: 10px 15px;
    border: none;
}

.openbtn:hover {
    background-color: #444;
}

#main {
    transition: margin-left .5s;
    padding: 16px;
}

.test {
    background-color: orange;
    height: 100%;
    font-size: 16px;
    outline: none;
    overflow-y: scroll;
    padding-top: 15px;
    position: fixed;
    top: 0;
    transition: 0.5s;
    width: 250px;
    z-index: 200;
}

.arrow {
    margin: 0 auto;
    text-align: center;
    position: relative;
    margin-top: 75px;
}

.arrow .b-go a {
    transition: 0.2s ease-in-out;
    scroll-behavior: smooth;
    transform: translateY(0px);
}

.arrow .b-go a:hover {
    transform: translateY(5px);
}

.h-bolder {
    background-color: #fff;
    text-align: center;
    padding: 7px;
    border-top: 1px solid #eee;
}

.h-bolder h4 {
    margin: 0;
    padding: 10px;
    font-size: 18px;
    color: $flat-blue;
}

/* iPhone icon */
.full-iPhone {
    background-color: #212121;
    height: 707px;
}

.iPhone {
    background-color: transparent;
    padding: 40px;
}

.iPhone svg,
.iPhone a {
    width: 250px;
    display: block;
    margin: 0 auto;
    text-align: center;
}

.goNaam {
    background-color: transparent;
}

.goNaam h3 {
    padding: 0;
    margin: 0;
    font-size: 22px;
    text-align: center;
    background-color: transparent;
    color: #fff;
}

.goNaam .dont {
    font-weight: bold;
}

.shand {
    position: relative;
    width: 100%;
    top: 0;
}

/* Video */
.v-control {
    background-color: red;
    margin: 0 auto;
    text-align: center;
}

/* /Video */

/* Section One */

.section-size {
    background-color: transparent;
}

.tra-text h1 {
    position: relative;
    z-index: 9999;
    text-align: center;
    transform: translate(5px, 360px);
    line-height: 2;
    color: #202020;
}

.mg-i {
    overflow: hidden;
}

.img {
    position: relative;
    right: -222px;
    float: right;
    opacity: 0.8;
}

.section-sz {
    /* background-color: #fafbfc; */
    background-color: #FDFDFD;
    margin-top: 0;
    height: auto;
    border-top: 1px solid #eee;
}

.m-bg {
    display: block;
    padding: 30px;
    height: auto;
    overflow: hidden;
}

.sect-size {
    background-color: transparent;
    /* border: 1px solid rgba(255, 255, 255, 0.111); */
    border-radius: 20px;
    padding: 0px;
}

.time button .zzz {
    margin-top: 30px;
    cursor: pointer;
    display: inline-block;
    /* width: 10%; */
}

.table {
    /* border: 2px solid #eee; */
}

#resultsNow {
    margin-top: 110px;
}

.table {
    border-right: 1px solid #e9e9e9;
}

.table tr th {
    font-size: 16px;
    font-weight: 100;
}

.table tr th span {
    display: inline-block;
    background-color: #212121;
    font-weight: 300;
    font-size: 18px;
    color: #fff;
    /* width: max-content; */
    box-sizing: border-box;
    height: auto;
    line-height: 40px;
    padding: 7px;
    /* border: 2px solid #7777ff; */
    border-radius: 10px;
    cursor: default;
}

.table tbody {
    font-size: 18px;
}

.time {
    display: block;
}

.time h5 {
    text-align: center;
    color: #666;
    line-height: 40px;
    font-size: 18px;
}

.bg-time {
    background-color: #ffffff;
    color: #212121;
    height: 100px;
    width: 100px;
    border-radius: 50%;
    border: 1px solid #111 !important;
}

.current-time {
    text-align: center;
    margin-top: 40px;
}

.current-time h5 {
    color: $flat-blue;
}

.first-sqr {
    margin-top: 20px;
}

.border-r {
    text-justify: auto;
    text-align: center;
}

.border-r h3 {
    font-weight: bold;
    color: $flat-blue;
}

.sqr {
    background-color: #fff;
    border: 1px solid #ddd;
    height: 140px;
    width: 140px;
    border-radius: 50%;
    padding: 0;
    display: block;
    text-align: center;
    margin: 20px auto;
    align-items: center;
    line-height: 2;
    color: $flat-blue;
    font-size: 25px;
    font-weight: bold;
}

.sqr h3 {
    padding-top: 20px;
}

td span {
    padding: 25px;
    border-radius: 50%;
    margin: 0;
    color: #666;
    background-color: transparent;
    display: inline-block;
    height: 90px;
    width: 90px;
}

.time button {
    float: right;
    background-color: #7777ff;
    color: #fff;
    font-size: 14px;
    padding: 5px;
}

.btn:hover {
    background-color: #7777f0;
    color: #fafafa !important;
}

/* /Section One */

/* Section Two */

